import React from 'react';
import { graphql, Link, PageRendererProps } from 'gatsby';
import styled from 'styled-components';

// components
import Layout from '../components/Layout';
import SEO from '../components/SEO';

const StyledBlogPostTemplate = styled.div`
  & {
    max-width: 680px;
    margin: auto;

    & h1.font-display-serif {
      letter-spacing: -1px;
    }
  }
`;

interface Props {
  data: {
    site: {
      siteMetadata: {
        title: string;
      };
    };
  };
  location: PageRendererProps['location'];
  pageContext: {
    isCreatedByStatefulCreatePages: boolean;
    // next: null;
    // previous: null;
    slug: string;
  };
}

const BlogPostTemplate: React.FC<Props> = props => {
  const {
    data,
    location,
    pageContext: { previous, next },
  } = props;
  console.log(props);
  const post = data.markdownRemark;
  const siteTitle = data.site.siteMetadata.title;
  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
      />
      <StyledBlogPostTemplate>
        <article>
          <header>
            <h1 className="font-display-serif text-5xl">
              {post.frontmatter.title}
            </h1>
            <h2 className="mt-3 text-gray-700 text-lg">
              {post.frontmatter.description}
            </h2>
            <p className="mt-1 text-gray-500 text-xs">
              {post.frontmatter.date}
            </p>
          </header>
          <hr className="mt-3 border-dashed" />
          <section
            className="remark"
            dangerouslySetInnerHTML={{ __html: post.html }}
          />
        </article>
        <hr className="mt-16 border-dashed" />
        <nav>
          <div className="flex flex-wrap justify-between py-12">
            <div>
              {previous && (
                <Link to={previous.fields.slug} rel="prev">
                  <div className="text-gray-600 text-sm">
                    <i className="bx bx-chevron-left" />
                    <span>{previous.frontmatter.title}</span>
                  </div>
                </Link>
              )}
            </div>
            <div>
              {next && (
                <Link to={next.fields.slug} rel="next">
                  <div className="text-gray-600 text-sm">
                    <span>{next.frontmatter.title}</span>
                    <i className="bx bx-chevron-right" />
                  </div>
                </Link>
              )}
            </div>
          </div>
        </nav>
        <div>
          <script
            src="https://utteranc.es/client.js"
            repo="lannex/blog-utterances"
            issue-term="pathname"
            theme="github-light"
            crossOrigin="anonymous"
            async
          />
        </div>
      </StyledBlogPostTemplate>
    </Layout>
  );
};

export default BlogPostTemplate;

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        tags
      }
    }
  }
`;
